* {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5rem;
  color: #F7F5E8;
  background: #2B2A30;
  position: relative;
  height: 100%;
}

img {
  max-width: 100%;
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  font-family: "Open Sans", sans-serif;
  color: #F7F5E8;
  margin-top: 20px;
  margin-bottom: 15px;
}

h1, .h1 {
  font-size: 4.375rem;
  line-height: 4.5rem;
  font-weight: 600;
}

h2, .h2 {
  font-size: 35px;
  line-height: 2.3125rem;
  font-weight: 600;
}

a {
  font-size: 1.25rem;
  font-weight: 400;
  text-decoration: none;
  color: #000;
}
a:hover {
  text-decoration: none;
}

p, div {
  font-size: 1.125rem;
  line-height: 1.875rem;
}

button {
  background: none;
  border: none;
  appearance: none;
  cursor: pointer;
}

form {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
form > * {
  flex: 0 0 100%;
}
form input,
form textarea {
  background: none;
  border: 2px solid black;
  font-family: "Open Sans", sans-serif;
  font-size: 1.25rem;
  font-weight: 400;
  padding: 25px 50px;
  border-radius: 50px;
  width: 100%;
  color: black;
}
form input::placeholder,
form textarea::placeholder {
  color: black;
}
form input[type=checkbox] {
  width: auto;
}

.introduction {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_6___});
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top left;
  height: 100vh;
  display: flex;
}
.introduction:after {
  content: "";
  height: 100%;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_7___});
  background-position: bottom right;
  background-repeat: no-repeat;
  background-size: 70px;
  position: absolute;
  top: -20px;
  left: 0;
  right: 20px;
  bottom: 0;
}
.introduction > .container {
  display: flex;
  position: relative;
}
.introduction > .container:before {
  content: "";
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  position: absolute;
  top: 15%;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 0;
  height: 85%;
}
.introduction__content {
  background-color: grey;
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 1;
}
.introduction .qualityList {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}
.introduction .qualityList__item {
  padding-right: 10px;
}
.introduction .qualityList__item:last-child {
  padding-right: 0;
}

.services__content {
  display: flex;
  flex-wrap: wrap;
  gap: 25px;
  justify-content: center;
  align-items: center;
}
.services__content__title {
  flex: 0 0 100%;
}
.services__card {
  border: 4px solid #FFC25C;
  flex: 0 1 48.5%;
}
.services__card--big {
  border: 10px solid #FFC25C;
  flex: 0 0 100%;
}
.services__card__content {
  display: flex;
  justify-content: center;
}
.services__card__content img {
  width: 90px;
  height: 90px;
}
.services__card__title {
  text-align: center;
}

.uppercase {
  text-transform: uppercase;
}

.txt-colored {
  color: #FFC25C;
